.expense-date {
	display: flex;
	flex-direction: column;
	width: 4.5rem;
	height: 5.5rem;
	border: 1px solid #ececec;
	background-color: #856e6e;
	color: white;
	border-radius: 12px;
	align-items: center;
	justify-content: center;
	box-shadow: #b7abab;
}

.expense-date__month {
	font-size: 0.75rem;
	font-weight: bold;
}

.expense-date__year {
	font-size: 0.75rem;
}

.expense-date__day {
	font-size: 1.5rem;
	font-weight: bold;
}
